@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



:root{
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52,58,64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-black:#000000;
  --primary-transparent-black: rgba(0,0,0,.7);
  --primary-green: green;
}


body{
  font-family: 'Roboto Condensed', sans-serif !important;
} 
/************************NAVBAR***********************************/

nav{
  z-index: 2;
}
.navbar{
  background-color: rgb(52,58,64);
  background-color: var(--primary-dark);
}
.logo{
  width: 5.25rem;

}

nav a.nav-link{
  letter-spacing: .0375rem;
  cursor: pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav li a{
  font-size: .9rem;
  color: white !important;
  margin-right: .75rem;
  margin-left: .75rem;
  border-top: .1875rem solid rgb(52,58,64);
  border-top: .1875rem solid var(--primary-dark);
}



.navbar-nav li a:focus,.navbar-nav li a:hover{
  border-top: .1875rem solid #f9ab00;
  border-top: .1875rem solid var(--primary-orange);
}

.navbar-brand{
  max-width: 7.25rem;
  padding: 0 !important;
  height: 6rem;
}

@media(max-width: 768px)
{
  .navbar-brand{
    max-width: 7rem;
  }
}

/************************Header***********************************/

.header-wrapper{
  position: relative;
  background: url(/React-Portfolio-Project/static/media/wall-and-laptop-background.f9e274f4.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;


}

.main-info{
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  justify-content: center;
  align-items: center;
  text-align: center;

  z-index: 1;
}

.main-info h1{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

@media(max-width: 768px)
{
  .main-info h1{
    font-size: 1.7rem !important;
  }
}

/**************************TYPED TEXT***************************/

.typed-text{
  font-size: 2rem;
  color: #fff;
}

@media(max-width: 768px)
{
  .typed-text{
    font-size: 1.3rem !important;
    color: #fff;

  }
}

/*********************** Header button *****************/

.btn-main-offer{
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: .625rem .75rem .75rem .75rem;
  color: #fff;
  margin-top: 2rem;
}

.btn-main-offer:hover{
  text-decoration: none;
  background-color: #ef4035;
  background-color: var(--primary-red);
  transition: .2s ease-in-out;
}
.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}


@media(max-width: 768px)
{
  .particles-canvas{
    display: none;
  }
}

/************************** ABOUT ME *****************/

.photo-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img{
  width: 40%;
  border-radius: 50%;
  border:  2px solid #f9ab00;
  border:  2px solid var(--primary-orange);
}
.about-heading{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

@media(max-width: 468px)
{
  .profile-img{
    
  }
}

/************************** SERVICES *******/
.services{
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);

}
.services h1{
  color:rgb(52,58,64);
  color:var(--primary-dark);
  text-transform: uppercase;
}
.services .circle{
  position: relative;
  margin: .375rem auto;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}
.services .icon{
  font-size: 1.4rem;
  color: #fff !important;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.services .box{
  height: 16rem;
  border-bottom: .3125rem solid #f9ab00;
  border-bottom: .3125rem solid var(--primary-orange);
  padding: .625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  transition: .3s ease-in-out;
}

.services .box:hover{
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: .3125rem solid rgb(52,58,64);
  border-bottom: .3125rem solid var(--primary-dark);
}
.services .box:hover .circle{
  background: #fff;
}

.services .box:hover .icon{
  background: rgb(52,58,64);
  background: var(--primary-dark);
}
.services .box:hover p{
  color: #fff;
}

@media (max-width:768px) {

  
}

/****************************Experience************/

.experience{
  padding: .7rem;
}
.experience-wrapper{
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1{
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

.experience-wrapper::before{
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -.0625rem;
  width: .125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block{
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
  
}

.timeline-block-right{
  float: right;
}
.timeline-block-left{
  float: left;
  direction: rtl;
}
.marker{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52,58,64);
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content{
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3{
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}

@media (max-width:768px) 
{
    .experience-wrapper::before{
      left: .5rem;
      margin-bottom: 1.875rem;
    }

    .timeline-block-right{
      float: none;
    }

    .timeline-block-left{
      float: none;
      direction: ltr;
    }
    .experience-wrapper{
      padding: 0;
    }
    .timeline-block{
      width: 100%;
    }
}

/****************************Portfolio************/
.portfolio-wrapper{
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1{
  color: #f9ab00;
  color: var(--primary-orange)
}
.portfolio-image{
  width: 15rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}

.portfolio-image-box{
  position: relative;
  margin: 1rem;
  width: auto;
}

/**********************OVERFLOW***************/
.overflow{
  position: absolute;
  opacity: 0%;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow{
  opacity: 60%;
  transition: 0.3s ease-in-out;

}

.portfolio-icon{
  position: absolute;
  opacity: 0; 
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon{
  opacity: 70%;
  transition: .3s ease-in-out;
  cursor: pointer;
}
/**************** Popupbox **************/

.portfolio-image-popupbox{
  width: 45rem;
  padding: .5rem;
}

.hyper-link{
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);
}
.hyper-link:hover{
  color: #ef4035;
  color: var(--primary-red);
}

@media (max-width:768px) {
  .portfolio-image-popupbox{
    width: 100%;
  }
  
}

@media (max-height:500px) {
  .popupbox-wrapper{
    height: 100%;
  }

  .portfolio-image-popupbox{
    width: 60%;
  }
}

@media (max-height:298px) {
  .popupbox-wrapper{
    height: 90%;
  }

  .portfolio-image-popupbox{
    width: 30%;
  }
}

/**********************TESTIMONIALS************************/
.testimonials{
  background: url(/React-Portfolio-Project/static/media/testimonials-bg.75612be7.jpg);
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1{
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content{
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgba(0,0,0,.7);
  background: var(--primary-transparent-black);
}

.myCarousel{
  color: #fff;
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.carousel .slide img{
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide{
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.myCarousel h3{
  color: #f9ab00;
  color: var(--primary-orange);
  font-weight: 600;
  text-transform: uppercase;
}
.myCarousel p{
  color: #ccd1d9;
  color: var(--primary-grey);
}

.carousel-root{
  margin: auto !important;
  margin-top: 3% !important;
}

/************CONTACT ME ****************/

.contacts {
  background: rgb(52,58,64);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1{
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p{
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;

}

/********* Inputs ************/
.contacts input, .contacts textarea{
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: .125rem solid #666666;
  border-bottom: .125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #ccd1d9;
  color: var(--primary-grey);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container{
padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus{
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: .125rem solid #666666;
  border-bottom: .125rem solid var(--primary-dark-grey);
}

.contacts textarea{
  height: 12.4rem !important;
}

.contact-btn{
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line{
  position: relative;
  top: -.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}
input:focus + .line,textarea:focus+.line{
  width:100%;
  transition: width .5s ease-in-out;
}

input.form-control,textarea.form-control{
  margin: -0.3rem 0;
}

.error-message{
  color: #fff;
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;

}

.success{
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}
/***** Mobile contact ****/

@media (max-width: 768px) {
  .contacts .container{
    padding: 0 1rem;
  }

  .contacts{
    overflow-x: hidden;
  }
  .contacts p{
    padding: 0 0.4rem;
  }
  
}

/********************FOOTER**************/

.footer{
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a{
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover{
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor:pointer;
}

.footer p{
  margin: 0;
}
